.coach {
  background-color: $color-default-white;
}

.coach__wrapper {
  padding-left: $padding-dst;
  padding-right: $padding-dst;
  padding-bottom: 82px;
  position: relative;

  @include vp-1199 {
    padding-left: 101px;
    padding-right: 101px;
    padding-bottom: 83px;
  }

  @include vp-767 {
    padding-left: $padding-mbl;
    padding-right: $padding-mbl;
    padding-bottom: 50px;
  }
}

.coach-button--prev {
  position: absolute;
  width: 56px;
  height: 56px;
  border: 1px solid transparent;
  left: calc((23 / 1200) * 100) + 0%;
  top: 230px;

  @include vp-1199 {
    width: 56px;
    height: 56px;
    left: calc((25 / 768) * 100) + 0%;
    top: 213px;
  }

  @include vp-767 {
    width: 47px;
    height: 47px;
    left: 1px;
    top: 210px;
    z-index: 3;
  }

  @include hover {
    border: 1px solid #102654;
    border-radius: 50%;

    &:active {
      background-color: #102654;
    }

    &:disabled {
      opacity: "0.3";
    }
  }
}

.coach-button--prev svg {
  position: absolute;
  color: #102654;
  left: calc((383 / 1200) * 100) + 0%;
  top: 10px;

  @include vp-1199 {
    left: calc((285 / 768) * 100) + 0%;
    top: 7px;
  }

  @include vp-767 {
    width: 10px;
    height: 19px;
    left: calc((103 / 320) * 100) + 0%;
    top: 15px;
  }

  &:active {
    color: $color-default-white;
  }
}

.coach-button--next {
  position: absolute;
  width: 56px;
  height: 56px;
  border: 1px solid transparent;
  right: calc((23 / 1200) * 100) + 0%;
  top: 230px;

  @include vp-1199 {
    width: 56px;
    height: 56px;
    right: calc((25 / 768) * 100) + 0%;
    top: 213px;
  }

  @include vp-767 {
    width: 47px;
    height: 47px;
    right: 26px;
    top: 210px;
    z-index: 3;
  }

  @include hover {
    border: 1px solid #102654;
    border-radius: 50%;

    &:active {
      background-color: #102654;
    }

    &:disabled {
      opacity: "0.3";
    }
  }
}

.coach-button--next svg {
  position: absolute;
  color: #102654;
  right: calc((383 / 1200) * 100) + 0%;
  top: 10px;
  transform: rotate(180deg);

  @include vp-767 {
    width: 10px;
    height: 19px;
    right: calc((-60 / 320) * 100) + 0%;
    top: 15px;
  }

  &:active {
    color: $color-default-white;
  }
}

.coach__title {
  margin: 0;
  color: #102654;
  font-family: $ff-as;
  font-size: 3.38em;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  letter-spacing: 1.62px;
  padding-bottom: 27px;

  @include vp-1199 {
    font-size: 2.63em;
    text-align: center;
    padding-top: 10px;
    padding-bottom: 10px;
  }

  @include vp-767 {
    font-size: 2em;
    padding-top: 14px;
    padding-bottom: 23px;
  }
}

.coach__swiper {
  width: 100%;
  height: 100%;
  margin: 20px auto;
}

.coach__swiper-slide {
  text-align: center;
  font-size: 1.13em;
  background: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 260px;
  position: relative;
}

.coach__swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: relative;

  @include vp-767 {
    height: 274px;
  }
}

.coach__swiper-wrapper {
  gap: calc((11 / 1200) * 100) + 0%;
}

.coach-card__name {
  opacity: 1;
  font-family: $ff-rc;
  padding: 0;
  margin: 0;
  font-size: 1em;
  font-style: normal;
  font-weight: 700;
}

.coach-card {
  opacity: 0;
  position: absolute;

  @include hover {
    opacity: 1;
    width: 260px;
    background-color: rgba(16, 38, 84, 0.89);
    color: $color-default-white;
    padding-top: 50px;
    padding-bottom: 42px;
    padding-left: 26px;
    padding-right: 26px;
    font-family: $ff-rc;
  }
}

@include vp-1199 {
  @media (max-width: ($vp-1200 - 1px)) {
    .coach__hover {
      opacity: 0;
      position: absolute;

      &:active {
        opacity: 1;
        width: 260px;
        background-color: rgba(16, 38, 84, 0.89);
        color: $color-default-white;
        padding-top: 50px;
        padding-bottom: 42px;
        padding-left: 26px;
        padding-right: 26px;
        font-family: $ff-rc;
      }
    }
  }
}

.coach-card__line {
  color: #b6bdcc;
  font-size: 1em;
  font-style: normal;
  font-weight: 400;
  margin: 0;
  font-family: $ff-rc;
  line-height: 150%;
  padding-bottom: 28px;
}

.coach-card__list {
  margin: 0;
  list-style-type: disc;
}

.coach-card__item {
  text-align: left;
  font-size: 0.88em;
  font-style: normal;
  font-weight: 400;
  margin: 0;
  padding-bottom: 9px;
}

.coach__name {
  position: absolute;
  z-index: 2;
  bottom: 20px;
  color: $color-default-white;
  font-family: $ff-rc;
  font-size: 1.13em;
  font-style: normal;
  font-weight: 700;
  margin: 0;
}

.swiper-slide:hover .coach__name {
  display: none;
}
