.footer {
  background: #030a1a;
}

.footer__wrapper {
  padding-top: 41px;
  padding-bottom: 32px;
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  padding-left: $padding-dst;
  padding-right: $padding-dst;

  @include vp-1199 {
    grid-template-columns: min-content auto min-content;
    padding-left: $padding-tbl;
    padding-right: $padding-tbl;
    padding-top: 45px;
  }

  @include vp-767 {
    padding-left: calc((14 / 320) * 100) + 0%;
    padding-right: $padding-tbl;
    padding-bottom: 85px;
    display: block;
    position: relative;
  }
}

.footer__logo {
  width: 5.5em;
  height: 2em;
  position: relative;

  @include vp-1199 {
    width: 5.13em;
    height: 1.13em;
  }

  @include vp-767 {
    width: 4.25em;
    height: 0.94em;
    position: absolute;
    top: 51px;
  }
}

.footer__svg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.footer-nav__list {
  margin: 0;
  list-style-type: none;
  display: flex;
  justify-content: center;
  gap: 2.5em;

  @include vp-1199 {
    gap: calc((46 / 768) * 100) + 0%;
    padding-left: 13px;
  }

  @include vp-767 {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 57px;
    row-gap: 16px;
    padding: 0;
  }
}

.footer-nav__item--first {
  display: none;

  @include vp-767 {
    display: block;
  }
}

.footer-nav__item--second {
  @include vp-767 {
    order: 1;
  }
}

.footer-nav__item--third {
  @include vp-767 {
    order: 5;
  }
}

.footer-nav__item--four {
  @include vp-767 {
    order: 0;
  }
}

.footer-nav__item--five {
  @include vp-767 {
    order: 4;
  }
}

.footer-nav__item--six {
  @include vp-767 {
    order: 6;
  }
}

.footer-nav__link {
  font-family: $ff-rc;
  font-size: 0.88em;
  font-weight: 400;
  line-height: normal;
  padding-top: 8px;
  color: $color-default-white;

  @include hover {
    border-bottom: 1px solid $color-default-white;

    &:active {
      border: 1px solid $color-default-white;
    }

    &:disabled {
      opacity: 0.4;
    }
  }

  @include vp-767 {
    font-size: 1em;
  }
}

.footer__social-list {
  margin: 0;
  list-style-type: none;
  display: flex;
  gap: 10px;
  justify-content: center;
  padding-left: 13.88em;

  @include vp-1199 {
    padding-left: 0;
  }

  @include vp-767 {
    position: absolute;
    top: 183px;
    left: 16px;
  }
}

.footer__social-item svg {
  background: $color-default-white;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  position: relative;

  @include hover {
    background: #103aae;
    color: $color-default-white;
  }

  &:active {
    background: #102654;
  }

  &:disabled {
    opacity: "0.3";
  }
}
